@media (max-width: 1200px) {
	.menu {
		&_item {
			padding-right: 20px; }
		&_link {
			font-size: 13px; } }
	.promo {
		&-header {
			font-size: 50px; }
		&-subheader {
			font-size: 36px;
			line-height: 39px; } }
	.mobile {
		&_item {
			width: 294px; } } }

@media (max-width: 991px) {
	.menu {
		&_item {
			padding-right: 8px; }
		&_link {
			font-size: 10px; } }
	.subheader {
		&-logo {
			img {
				width: 100%; } }
		&-oficial {
			font-size: 11px; }
		&-phone {
			font-size: 18px; }
		&-btn {
			width: 100%;
			height: 40px;
			margin-top: 21px;
			font-size: 13px; } }
	.promo {
		&-header {
			font-size: 40px; }
		&-subheader {
			font-size: 30px;
			line-height: 32px; } }
	.reasons {
		&_block {
			flex-direction: column;
			align-items: center;
			height: auto;
			margin-top: 21px; }
		&_descr {
			width: 300px;
			margin-top: 20px; }
		.title {
			font-size: 34px; } } }

@media (max-width: 767px) {
	.menu {
		position: fixed;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		top: 0;
		left: -100%;
		width: 50%;
		height: 100vh;
		padding: 20px 15px;
		background-color: rgba(0,0,0, .8);
		transition: 0.6s all;
		&_active {
			left: 0; }
		&_item {
			padding-right: 0;
			border: none;
			margin: 10px 0; }
		&_link {
			font-size: 20px; } } }
