header {
	position: relative;
	height: 50px; }

nav {
	position: fixed;
	height: 50px;
	width: 100%;
	background-color: #212121;
	z-index: 3; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	&_link {
		font-size: 15px;
		font-weight: 300;
		color: #ffffff;
		&:hover {
			text-decoration: none;
			color: $color_1; } }
	&_item {
		border-right: 2px dotted black;
		padding-right: 28px;
		&:last-child {
			border-right: none; } } }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	/*Для нормального отображения шапки с лого */
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);
	/* канал .6 делает прозрачной шапку, сами элементы на ней не трогает */
	&-logo {
		display: block;
		/*Для того, чтобы наша ссылка получила отступ вниз. у строк не работают сверху отступы! */
		max-width: 100%;
		/* если в будущем картинка поменяется и станет больше для этого ставим */
		margin-top: 16px; }
	&-oficial {
		opacity: 0.5;
		margin-top: 6px;
		font-size: 13px;
		font-weight: 400;
		color: #ffffff; }
	&-call {
		margin-top: 19px;
		text-align: right;
		font-size: 12px;
		font-weight: 300;
		color: #ffffff; }
	&-phone {
		display: block;
		margin-top: 8px;
		line-height: 12px;
		text-align: right;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		color: #ffffff; }
	&-btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		border: none;
		border-radius: 4px;
		box-shadow: inset 0px -3px 0 rgba(0, 0, 0, 0.12);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		color: #ffffff;
		@include btn_hover; } }

.promo {
	min-height: 800px;
	background: url("../img/background-promo.jpg") center (center / cover) no-repeat;
	padding: 150px 0 135px 0;
	/*??? */
	&-header {
		margin: 0;
		font-size: 60px;
		font-weight: 700;
		text-transform: uppercase;
		color: #ffffff;
		text-align: center;
		line-height: 60px; }
	&-subheader {
		font-size: 36px;
		font-weight: 700;
		text-transform: uppercase;
		color: #ffffff;
		line-height: 15px;
		text-align: center;
		margin: 0;
		margin-top: 22px; }
	&-descr {
		margin-top: 23px;
		font-size: 18px;
		line-height: 24px;
		font-weight: 400;
		color: #ffffff;
		text-align: center; }
	&-btn {
		display: block;
		/*Для того чтобы дать кнопке стать по центру дать ей марджины */
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		border: none;
		border-radius: 4px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		color: #ffffff;
		@include btn_hover; } }

.reasons {
	padding: 71px 0 65px 0;
	&_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px;
		height: 147px; }
	&_round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 113px;
		height: 113px;
		background-color: $color_1;
		border-radius: 100%; }
	&_descr {
		text-align: justify;
		width: 343px; }
	&_subtitle {
		line-height: 21px;
		color: $color_1;
		font-size: 17px;
		font-weight: 400; }
	&_text {
		color: #202020;
		font-size: 15px;
		font-weight: 400;
		line-height: 21px; } }

.label {
	position: relative;
	margin: 0 auto;
	width: 107px;
	padding: 5px 9px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 400;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_white {
		background-color: #fff;
		color: #000; }
	&_min {
		width: 76px; } }

.subtitle {
	position: relative;
	margin-top: 19px;
	font-size: 17px;
	font-weight: 400;
	line-height: 1;
	color: #222222;
	text-align: center;
	z-index: 2;
	&_white {
		color: #fff;
		z-index: 2; } }

.title {
	position: relative;
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	line-height: 1;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_white {
		color: #fff; } }

.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0;
	background: url("../img/mobile_bg.png") center (center / cover) no-repeat;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
				background: url("../img/ms1.jpg") center (center / cover) no-repeat; }
		&_2 {
				background: url("../img/ms2.jpg") center (center / cover) no-repeat; }
		&_3 {
				background: url("../img/ms3.jpg") center (center / cover) no-repeat; }
		&_4 {
				background: url("../img/ms4.jpg") center (center / cover) no-repeat; }
		&_5 {
				background: url("../img/ms5.jpg") center (center / cover) no-repeat; }
		&_6 {
				background: url("../img/ms6.jpg") center (center / cover) no-repeat; }
		&_subtitle {
			font-size: 24px;
			line-height: 31px;
			font-weight: 400;
			text-transform: uppercase;
			color: #ffffff;
			text-align: center; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translate(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			margin: 0 auto;
			border-radius: 100%;
			cursor: pointer;
			&:before {
				content: "";
				display: block;
				width: 14px;
				height: 2px;
				background-color: $color_1;
				transition: 0.3s all; }
			&:after {
				content: "";
				position: absolute;
				left: 50%;
				transform: translate(-50%);
				display: block;
				width: 2px;
				height: 14px;
				background-color: $color_1;
				transition: 0.3s all; }
			&:hover {
				&:before, &:after {
					background-color: $color_2; } } } } }

.choice {
	position: relative;
	min-height: 895px;
	padding: 57px 0 138px 0;
	background: url("../img/mobile_bg.png") center (center / cover) no-repeat;
	&_img {
		position: relative;
		display: block;
		margin: 56px auto 0 auto;
		z-index: 2; }
	&_descr {
		font-size: 17px;
		font-weight: 400;
		color: #222222;
		text-align: center;
		margin-top: 4px; }
	&_black {
		position: absolute;
		top: 0;
		left: 0;
		height: 499px;
		width: 100%;
		background-color: #070716;
		z-index: 1; } }

.terms {
	padding: 68px 0 84px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }
	&_title {
		font-size: 22px;
		line-height: 1px;
		font-weight: 400;
		color: #1ca8c3; }
	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px;
		li {
			position: relative;
			font-size: 17px;
			font-weight: 400;
			color: #222222;
			line-height: 24px;
			margin-bottom: 10px;
			&:before {
				position: absolute;
				left: -32px;
				top: 3px;
				content: "";
				display: block;
				width: 21px;
				height: 20px;
				background: url(../icons/par.png) center (center / cover) no-repeat; } } }
	&_descr {
		font-size: 17px;
		font-weight: 400;
		color: #1ca8c3; }
	.warning {
		.terms_title {
			color: $color_2; }
		.terms_list {
			li {
				&::before {
					background: url(../icons/par_red.png) center (center / cover) no-repeat; } } } } }
.world_map {
	position: relative;
	min-height: 717px;
	padding: 49px 0;
	background: url(../img/map_bg.jpg) center (center / cover) no-repeat;
	&_link {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 49px;
		text-align: center;
		font-size: 15px;
		font-weight: 400;
		text-transform: uppercase;
		color: #1fbad6;
		&:hover {
			text-decoration: none;
			color: #fff; } } }

footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }

.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			i {
				color: #fff; } } }
	&_links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: #1fbad6;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center; } } }
